@import '../variables';

.pizza-block {
  max-width: 280px;
  width: 100%;
  text-align: center;
  margin-bottom: 65px;
  margin-right: 35px;


  &__image {
    max-width: 260px;
    width: 100%;
  }

  &__title {
    font-size: 20px;
    font-weight: 900;
    letter-spacing: 1%;
    margin-bottom: 20px;
  }

  &__selector {
    display: flex;
    background-color: #f3f3f3;
    border-radius: 10px;
    flex-direction: column;
    padding: 6px;

    ul {
      display: flex;
      flex: 1;

      &:first-of-type {
        margin-bottom: 6px;
      }

      li {
        padding: 8px;
        flex: 1;
        cursor: pointer;
        font-weight: 600;
        font-size: 14px;
        @include noselect();

        &.active {
          background: #ffffff;
          box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.04);
          border-radius: 5px;
          cursor: auto;
        }

        &.disablet {
          opacity: 0.4;
          pointer-events: none;
        }
      }
    }
  }

  &__bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 20px;
  }

  &__price {
    font-weight: bold;
    font-size: 22px;
    line-height: 27px;
    letter-spacing: 0.015em;
  }
}


@media (max-width: 576px) {
  .pizza-block {
    margin-right: 0;
  }
}

@media (max-width: 345px) {
  .pizza-block__bottom {
    flex-wrap: wrap;
    justify-content: center;
  }
  .pizza-block__price {
    margin-bottom: 20px;
  }
}