@import '../variables';

.header {
  border-bottom: 1px solid $gray-line;
  padding: 40px 0;

  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  &__logo {
    display: flex;
    margin-right: 20px;

    img {
      margin-right: 15px;
    }

    h1 {
      color: #181818;
      font-size: 24px;
      letter-spacing: 1%;
      text-transform: uppercase;
      font-weight: 800;
    }

    p {
      color: #7b7b7b;
    }
  }
}


@media (max-width: 576px) {
  .header {
    .container {
      flex-wrap: wrap;
      justify-content: center;
    }

    &__logo {
      margin-right: 0;
      margin-bottom: 30px;
    }
  }
}